import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  fileContent: any;
  fileContent1: any;

  freq: any = [0, 20, 30, 40, 60, 80, 100, 150, 200, 300, 400, 600, 800, 1000, 1500, 2000, 3000, 5000, 8000, 10000, 15000, 20000, 22000, 24000];
  freqInterval: any = 20

  allFrequncies = [];
  dispAllFrequncies: any;
  dispSelectedFreq;
  freqCounterMap = {};
  dispFreqCounterMap = {};

  dispFilteredMap = {};

  selectedFreq: any;
  public onChange(fileList: FileList): void {
    if ('string' === (typeof this.freq)) {
      this.freq = this.freq.split(', ');
      for (let i in this.freq) {
        this.freq[i] = Number(this.freq[i]);
      }
    }

    if ('string' === (typeof this.freqInterval)) {
      this.freqInterval = Number(this.freqInterval);
    }
    let file = fileList[0];
    let fileReader: FileReader = new FileReader();
    let self = this;
    // this.freq = this.freq.replace(/\s/g, "");
    // let supportedFreq = this.freq.split(',');
    // console.log(supportedFreq);
    fileReader.onloadend = (x) => {
      this.fileContent = {};
      this.fileContent1 = {};
      let fileContent = '' + fileReader.result;
      var allTextLines: any = fileContent.split(/\r\n|\n/);
      var entries = allTextLines[0].split(',');
      let freq = [];
      let gain = [];

      for (let i in allTextLines) {
        for (let j in allTextLines[i].split(',')) {
          if (j === '0' && i !== '0' && allTextLines[i].split(',')[j] !== '') {
            freq.push(allTextLines[i].split(',')[j]);
          }
          if (i === '0' && j !== '0') {
            gain.push(allTextLines[i].split(',')[j])
          }
          if (j !== '0' && i !== '0') {
            if (!this.fileContent[entries[j]]) {
              this.fileContent[entries[j]] = [];
            }
            this.fileContent[entries[j]].push(Number(allTextLines[i].split(',')[j]));
          }
        }
      }

      this.allFrequncies = freq;
      this.freqCounterMap = this.calculateNeededFreq();

      for (let i in allTextLines) {
        for (let j in allTextLines[i].split(',')) {

          if (i !== '0') {
            if (!this.fileContent1[entries[j]]) {
              this.fileContent1[entries[j]] = [];
            }
            let val = Number(allTextLines[i].split(',')[0]);
            if (this.selectedFreq.has(val)) {
              if (j !== '0') {
                this.fileContent1[entries[j]].push(Number(allTextLines[i].split(',')[j]));
                console.log(Number(allTextLines[i].split(',')[0]));
              }
            }
          }

        }
      }

      this.dispFreqCounterMap = JSON.stringify(this.freqCounterMap);
      this.dispAllFrequncies = JSON.stringify(this.allFrequncies);
      this.fileContent = JSON.stringify(this.fileContent);
      this.dispFilteredMap = JSON.stringify(this.fileContent1);
      console.log(this.dispFilteredMap);
    }
    fileReader.readAsText(file);
  }

  calculateNeededFreq() {
    let freqCountMap = {};
    let freqCountMap1 = {};
    let considerFreq = new Set();
    for (let i in this.freq) {
      let i1 = Number(i);
      let freqCnt = 0;
      if (Number(i1) + 1 < this.freq.length) {
        for (let j of this.allFrequncies) {
          if (j > this.freq[i] && j < this.freq[i1 + 1]) {
            if (!freqCountMap1[this.freq[i] + '-' + this.freq[i1 + 1]]) {
              freqCountMap1[this.freq[i] + '-' + this.freq[i1 + 1]] = []
            }
            freqCountMap1[this.freq[i] + '-' + this.freq[i1 + 1]].push(j);
            freqCnt++;
          }
        }
        freqCountMap[this.freq[i]] = freqCnt;
      }
    }

    for (let i in this.freq) {
      let i1 = Number(i);
      let freqCnt = 0;
      let interval = 1;
      if (Number(i1) + 1 < this.freq.length) {
        if (this.freq[i] / this.freqInterval !== 0) {
          interval = parseInt('' + Number(freqCountMap[this.freq[i]] / this.freqInterval));
        }
        if (interval == 0) interval = 1;
        for (let j = 0; j < this.allFrequncies.length; j++) {
          if (this.allFrequncies[j] > this.freq[i] && this.allFrequncies[j] < this.freq[i1 + 1]) {
            for (let k = j; this.allFrequncies[k] < this.freq[i1 + 1]; k = k + interval) {
              considerFreq.add(Number(this.allFrequncies[k]));
            }
            break;
          }
        }
      }
    }
    this.selectedFreq = considerFreq;
    this.dispSelectedFreq = [...new Set(considerFreq)];
    return freqCountMap1;
  }

  stringify(x) {
    JSON.stringify(x);
  }

}
